import { Component, OnInit, OnDestroy } from '@angular/core';
import { Router } from '@angular/router';
import { Subject } from 'rxjs';

import { MatTableDataSource } from '@angular/material';
import { RTLService } from '../../shared/services/rtl.service';
import { LoggerService } from '../../shared/services/logger.service';
import { GetInfo } from '../../shared/models/getInfo';

@Component({
  selector: 'rtl-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit, OnDestroy {
  public fees: any;
  public displayedColumns = ['day_fee_sum', 'week_fee_sum', 'month_fee_sum'];
  public information: GetInfo = {};
  public activeChannels = 0;
  public totalBalance = '';
  public channelBalance = '';
  private unsubInfo = new Subject();
  private unsubFees = new Subject();
  private unsubBalBlock = new Subject();
  private unsubBalChannel = new Subject();

  constructor(private rtlService: RTLService, private logger: LoggerService, private router: Router) {}

  ngOnInit() {
    this.rtlService.getInfo();
    this.rtlService.informationUpdated
    .takeUntil(this.unsubInfo)
    .subscribe((data: any) => {
      if (data.error) {
        this.logger.info('Redirecting to Unlock');
        this.router.navigate(['/start']);
        return;
      }
      this.logger.info(data);
      this.information = data;
    });
    this.rtlService.getFees()
    .takeUntil(this.unsubFees)
    .subscribe((data: any) => {
      this.logger.info(data);
      this.fees = (undefined === data) ? new MatTableDataSource<number>([]) :
        new MatTableDataSource<any>([...[{day_fee_sum: data.fees.day_fee_sum, week_fee_sum: data.fees.week_fee_sum, month_fee_sum: data.fees.month_fee_sum}]]);
      this.logger.info(this.fees);
    });
    this.rtlService.getBalance('blockchain')
    .takeUntil(this.unsubBalBlock)
    .subscribe((data: any) => {
      this.totalBalance = (undefined === data.balance.total_balance) ? 0 : data.balance.total_balance;
      this.totalBalance = this.totalBalance.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ',');
      this.logger.info(this.totalBalance);
    });
    this.rtlService.getBalance('channels')
    .takeUntil(this.unsubBalChannel)
    .subscribe((data: any) => {
      this.channelBalance = (undefined === data.balance.balance) ? 0 : data.balance.balance;
      this.channelBalance = this.channelBalance.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ',');
      this.logger.info(this.channelBalance);
    });
  }

  ngOnDestroy() {
    this.unsubInfo.next();
    this.unsubInfo.complete();
    this.unsubFees.next();
    this.unsubFees.complete();
    this.unsubBalBlock.next();
    this.unsubBalBlock.complete();
    this.unsubBalChannel.next();
    this.unsubBalChannel.complete();
  }

}
