import { Component, OnInit, OnDestroy, ViewEncapsulation } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { Subject } from 'rxjs/Subject';
import 'rxjs/add/operator/takeUntil';

import { Settings } from '../../../shared/models/app.settings';
import { MenuService } from '../menu/menu.service';
import { LoggerService } from '../../../shared/services/logger.service';
import { RTLService } from '../../../shared/services/rtl.service';
import { GetInfo } from '../../../shared/models/getInfo';

@Component({
  selector: 'rtl-sidenav',
  templateUrl: './sidenav.component.html',
  styleUrls: ['./sidenav.component.scss'],
  encapsulation: ViewEncapsulation.None,
  providers: [MenuService]
})
export class SidenavComponent implements OnInit, OnDestroy {
  public userImage = '../assets/img/RTL1.jpg';
  public menuItems: Array<any>;
  public settings: Settings;
  private infoSub = new Subject<void>();
  private settingsSub = new Subject<void>();
  public information: GetInfo = {};

  constructor(public menuService: MenuService, private rtlService: RTLService, private logger: LoggerService, private router: Router, private activateRoute: ActivatedRoute) {
    this.settings = this.rtlService.getUISettings();
  }

  ngOnInit() {
    this.menuItems = this.menuService.getVerticalMenuItems();
    this.information = this.rtlService.readInformation();
    this.rtlService.informationUpdated
    .takeUntil(this.infoSub)
    .subscribe(data => {
      this.information = data;
      this.logger.info('Server Information Updated');
      this.logger.info(this.information);
    });
    this.rtlService.UIsettingsUpdated
    .takeUntil(this.settingsSub)
    .subscribe( settings => {
      this.settings = settings;
    });
  }

  ngOnDestroy() {
    this.infoSub.next();
    this.infoSub.complete();
    this.settingsSub.next();
    this.settingsSub.complete();
  }

  public closeSubMenus() {
    const menu = document.querySelector('.sidenav-menu-outer');
    if (menu) {
      for (let i = 0; i < menu.children[0].children.length; i++) {
        const child = menu.children[0].children[i];
        if (child) {
          if (child.children[0].classList.contains('expanded')) {
            child.children[0].classList.remove('expanded');
            child.children[1].classList.remove('show');
          }
        }
      }
    }
  }

}
