import { Component, OnInit, OnDestroy, ViewChild, ViewEncapsulation } from '@angular/core';
import { Subject } from 'rxjs';

import { MatTableDataSource, MatSort, MatDialog } from '@angular/material';
import { RTLService } from '../../shared/services/rtl.service';
import { Peer } from '../../shared/models/peers';
import { LoggerService } from '../../shared/services/logger.service';
import { AlertMessageComponent } from '../../theme/components/alert-message/alert-message.component';
import { SpinnerDialogComponent } from '../../theme/components/spinner-dialog/spinner-dialog.component';

@Component({
  selector: 'rtl-peers',
  templateUrl: './peers.component.html',
  styleUrls: ['./peers.component.css'],
  encapsulation: ViewEncapsulation.None
})
export class PeersComponent implements OnInit, OnDestroy {
  @ViewChild(MatSort) sort: MatSort;
  public position = 'above';
  public displayedColumns = ['pub_key', 'alias', 'address', 'bytes_sent', 'bytes_recv', 'sat_sent', 'sat_recv', 'inbound', 'ping_time'];
  public peerAddress = '';
  public invalidAddress = true;
  public peers: any;
  private peersJSONArr: Peer[] = [];
  private peersSub = new Subject();

  constructor(private rtlService: RTLService, private logger: LoggerService, public dialog: MatDialog) {}

  ngOnInit() {
    this.rtlService.getPeers()
    .takeUntil(this.peersSub)
    .subscribe((peers: Peer[]) => {
      this.peersJSONArr = peers;
      this.peers = (undefined === peers) ?  new MatTableDataSource([]) : new MatTableDataSource<Peer>([...this.peersJSONArr]);
      this.peers.data = this.peersJSONArr;
      this.peers.sort = this.sort;
      this.logger.info(this.peers);
    });
  }

  onAddPeer() {
    let dialogRef = this.dialog.open(SpinnerDialogComponent, { data: { message: 'Adding Peer...'}});
    let deviderIndex = this.peerAddress.search('@');
    let pubkey = this.peerAddress.substring(0, deviderIndex);
    let host = this.peerAddress.substring(deviderIndex + 1);

    this.rtlService.addPeer(pubkey, host)
    .subscribe((data:any) => {
      dialogRef.close();
      this.peersJSONArr.push({pub_key: pubkey, address: host});
      this.peers.data = this.peersJSONArr;
      this.peerAddress = '';
      this.logger.info(data);
      this.dialog.open(AlertMessageComponent, { width: '700px', data: {type: 'SUCCESS', message: 'Peer Added Successfully!'}});
    },
    (err: any) => {
      dialogRef.close();
      this.dialog.open(AlertMessageComponent, { width: '700px', data: {type: 'ERROR', message: err.error.error}});
      this.logger.error(err);
    });
  }

  validateAddress() {
    const address_regex = '^([a-zA-Z0-9]){1,66}@(([0-9]|[1-9][0-9]|1[0-9]{2}|2[0-4][0-9]|25[0-5])\.){3}([0-9]|[1-9][0-9]|1[0-9]{2}|2[0-4][0-9]|25[0-5]):[0-9]+$';
    this.invalidAddress = (this.peerAddress.search(address_regex) > -1) ? false : true;
  }

  ngOnDestroy() {
    this.peersSub.next();
    this.peersSub.complete();
  }
}
