import { Component, OnInit, OnDestroy, ViewChild, ViewEncapsulation } from '@angular/core';
import { Subject } from 'rxjs';

import { MatTableDataSource, MatSort, MatDialog } from '@angular/material';
import { RTLService } from '../../shared/services/rtl.service';
import { Channel } from '../../shared/models/channels';
import { Peer } from '../../shared/models/peers';
import { LoggerService } from '../../shared/services/logger.service';
import { AlertMessageComponent } from '../../theme/components/alert-message/alert-message.component';
import { SpinnerDialogComponent } from '../../theme/components/spinner-dialog/spinner-dialog.component';

@Component({
  selector: 'rtl-channels',
  templateUrl: './channels.component.html',
  styleUrls: ['./channels.component.css'],
  encapsulation: ViewEncapsulation.None
})
export class ChannelsComponent implements OnInit, OnDestroy {
  @ViewChild(MatSort) sort: MatSort;
  public position = 'below';
  public selectedPeer = '';
  public fundingAmount: number;
  public displayedColumns = [
    'active', 'chan_id', 'remote_pubkey', 'capacity', 'local_balance', 'remote_balance',
    'total_satoshis_sent', 'total_satoshis_received', 'commit_fee'
  ];
  public channels: any;
  public activeChannels = 0;
  public inactiveChannels = 0;
  public pendingChannels = 0;
  public peers: Peer[] = [];
  private channelSub = new Subject();
  private pendingCSub = new Subject();
  private peersSub = new Subject();

  constructor(private rtlService: RTLService, private logger: LoggerService, public dialog: MatDialog) {}

  ngOnInit() {
    this.rtlService.getPeers()
    .takeUntil(this.peersSub)
    .subscribe((data: any[]) => {
      this.peers = data;
    });
    this.rtlService.getChannels('all', '')
    .takeUntil(this.channelSub)
    .subscribe((data: any) => {
      if (undefined === data.channels) {
        data.channels = [];
      }
      data.channels.sort(function(a, b) {
        return (a.active === b.active) ? 0 : ((a.active) ? -1 : 1);
      });
      data.channels.filter(channel => {
        if (channel.active === true) {
          channel.active = 'Active';
          this.activeChannels++;
        } else {
          channel.active = 'Inactive';
          this.inactiveChannels++;
        }
      });
      this.channels = new MatTableDataSource<Channel>([...data.channels]);
      this.channels.sort = this.sort;
      this.logger.info(this.channels);
    });
    this.rtlService.getChannels('pending', '')
    .takeUntil(this.pendingCSub)
    .subscribe((data: any) => {
      this.pendingChannels = (undefined === data.pending_open_channels) ? 0 : data.pending_open_channels.length;
      this.logger.info(this.pendingChannels);
    });
  }

  onAddChannel() {
    let dialogRef = this.dialog.open(SpinnerDialogComponent, { data: { message: 'Opening Channel...'}});
    this.rtlService.addChannel(this.selectedPeer, this.fundingAmount)
    .subscribe(data => {
      dialogRef.close();
      this.selectedPeer = '';
      this.fundingAmount = null;
      this.logger.info(data);
      this.dialog.open(AlertMessageComponent, { width: '700px', data: {type: 'SUCCESS', message: 'Channel Added Successfully!'}});
      this.rtlService.getChannels('pending', '')
      .takeUntil(this.pendingCSub)
      .subscribe((data: any) => {
        this.pendingChannels = (undefined === data.pending_open_channels) ? 0 : data.pending_open_channels.length;
        this.logger.info(this.pendingChannels);
      });
    },
    (err: any) => {
      dialogRef.close();
      this.dialog.open(AlertMessageComponent, { width: '700px', data: {type: 'ERROR', message: err.error.error}});
      this.logger.error(err);
    });
  }

  ngOnDestroy() {
    this.channelSub.next();
    this.channelSub.complete();
    this.pendingCSub.next();
    this.pendingCSub.complete();
    this.peersSub.next();
    this.peersSub.complete();
  }
}
