import { Component, OnInit, OnDestroy } from '@angular/core';
import { ActivatedRoute, Router, ActivatedRouteSnapshot, UrlSegment, NavigationEnd } from '@angular/router';
import { Subject } from 'rxjs';
import { Title } from '@angular/platform-browser';
import { Settings } from '../../../shared/models/app.settings';
import { RTLService } from '../../../shared/services/rtl.service';

@Component({
  selector: 'rtl-breadcrumb',
  templateUrl: './breadcrumb.component.html',
  styleUrls: ['./breadcrumb.component.scss']
})
export class BreadcrumbComponent implements OnInit, OnDestroy {
  private unsubSettings = new Subject();
  public pageTitle: string;
  public breadcrumbs: {
    name: string;
    url: string
  }[] = [];

  public settings: Settings;
  constructor(public router: Router, public activatedRoute: ActivatedRoute, public title: Title, private rtlService: RTLService) {
    this.settings = this.rtlService.getUISettings();
  }

  ngOnInit() {
    this.router.events.subscribe(event => {
      if (event instanceof NavigationEnd) {
        this.breadcrumbs = [];
        this.parseRoute(this.router.routerState.snapshot.root);
        this.pageTitle = '';
        this.breadcrumbs.forEach(breadcrumb => {
          this.pageTitle += ' > ' + breadcrumb.name;
        });
        this.title.setTitle(this.settings.name + this.pageTitle);
      }
    });
    this.rtlService.UIsettingsUpdated
    .takeUntil(this.unsubSettings)
    .subscribe( settings => {
      this.settings = settings;
      if (window.innerWidth <= 768) {
        this.settings.menu = 'vertical';
        this.settings.sidenavIsOpened = false;
        this.settings.sidenavIsPinned = false;
      }
    });
  }

  private parseRoute(node: ActivatedRouteSnapshot) {
    if (node.data['breadcrumb']) {
      if (node.url.length) {
        let urlSegments: UrlSegment[] = [];
        node.pathFromRoot.forEach(routerState => {
          urlSegments = urlSegments.concat(routerState.url);
        });
        const url = urlSegments.map(urlSegment => {
          return urlSegment.path;
        }).join('/');
        this.breadcrumbs.push({
          name: node.data['breadcrumb'],
          url: '/' + url
        });
      }
    }
    if (node.firstChild) {
      this.parseRoute(node.firstChild);
    }
  }

  public closeSubMenus() {
    const menu = document.querySelector('.sidenav-menu-outer');
    if (menu) {
      for (let i = 0; i < menu.children[0].children.length; i++) {
        const child = menu.children[0].children[i];
        if (child) {
          if (child.children[0].classList.contains('expanded')) {
            child.children[0].classList.remove('expanded');
            child.children[1].classList.remove('show');
          }
        }
      }
    }
  }

  ngOnDestroy() {
    this.unsubSettings.next();
    this.unsubSettings.complete();
  }
}
