import { Component, OnInit, OnDestroy } from '@angular/core';

import { Settings } from './shared/models/app.settings';
import { RTLService } from './shared/services/rtl.service';
import { Subject } from 'rxjs';

@Component({
  selector: 'rtl-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent implements OnInit, OnDestroy {
  public settings: Settings;
  unsubscribe = new Subject();

  constructor(private rtlService: RTLService) {
    this.rtlService.fetchUISettings();
    this.settings = this.rtlService.getUISettings();
  }
  
  ngOnInit() {
    this.rtlService.UIsettingsUpdated
    .takeUntil(this.unsubscribe)
    .subscribe( settings => {
      this.settings = settings;
    });
  }

  ngOnDestroy() {
    this.unsubscribe.next();
    this.unsubscribe.complete();
  }
}
