import { Component, OnInit, Input, ViewEncapsulation, AfterViewInit, OnDestroy } from '@angular/core';
import { Router, NavigationEnd } from '@angular/router';
import { MenuService } from '../menu.service';
import { Subject } from 'rxjs';

import { Settings } from '../../../../shared/models/app.settings';
import { RTLService } from '../../../../shared/services/rtl.service';

@Component({
  selector: 'rtl-vertical-menu',
  templateUrl: './vertical-menu.component.html',
  styleUrls: ['./vertical-menu.component.scss'],
  encapsulation: ViewEncapsulation.None,
  providers: [MenuService]
})
export class VerticalMenuComponent implements OnInit, AfterViewInit, OnDestroy {
  @Input() menuItems;
  @Input() menuParentId;
  parentMenu: Array<any>;
  public settings: Settings;
  private unsubSettings = new Subject();

  constructor(public menuService: MenuService, public router: Router, private rtlService: RTLService) {
    this.settings = this.rtlService.getUISettings();
  }

  ngOnInit() {
    this.rtlService.UIsettingsUpdated
    .takeUntil(this.unsubSettings)
    .subscribe( settings => {
      this.settings = settings;
      if (window.innerWidth <= 768) {
        this.settings.menu = 'vertical';
        this.settings.sidenavIsOpened = false;
        this.settings.sidenavIsPinned = false;
      }
    });
    this.parentMenu = this.menuItems.filter(item => item.parentId === this.menuParentId);
  }

  ngAfterViewInit() {
    this.router.events.subscribe(event => {
      if (event instanceof NavigationEnd) {
        if (this.settings.fixedHeader) {
          const mainContent = document.getElementById('main-content');
          if (mainContent) {
            mainContent.scrollTop = 0;
          }
        } else {
          document.getElementsByClassName('mat-drawer-content')[0].scrollTop = 0;
        }
      }
    });
  }

  onClick(menuId) {
    this.menuService.toggleMenuItem(menuId);
    this.menuService.closeOtherSubMenus(this.menuItems, menuId);
  }

  ngOnDestroy() {
    this.unsubSettings.next();
    this.unsubSettings.complete();
  }

}
